.left-pane
    background: #15202b
    color: #fff
    min-height: 100vh
    max-width: 30vw
    width: 100%

    .container
        position: fixed

.container
    header
        display: flex
        flex-direction: column
header svg
    height: 1.9em
    margin-top: 0.7em
    margin-bottom: 0.7em

nav svg
    height: 1.3em
    margin-right: 0.9em

nav
    padding-left: 3.3em

nav > *, button.more
    text-decoration: none
    color: #fff
    font-size: 1.3em
    font-weight: 500
    width: 100%
    cursor: pointer

    span
        display: flex
        align-items: center
        height: 2.4em
        width: fit-content
        padding: 0 0.7em

    &:hover span
        color: #1da1f2
        border-radius: 1.5em
        background: #192734

nav .selected
    color: #1da1f2

button
    background: unset
    border: unset

button.tweet-btn, .logOut--btn
    display: block
    background: #1da1f2
    color: #fff
    width: 14em
    height: 3em
    border-radius: 1.5em
    font-size: 1em
    margin-top: 0.5em
    margin-left: 3.7em
    cursor: pointer
    text-align: center
    font-weight: bold

footer
    position: fixed
    bottom: 1.5em
    margin-left: 0.5em
    display: flex
    justify-content: space-between
    width: 27vw

    button.account
        display: grid
        grid-template-columns: 1fr 1fr
        align-items: center
        color: #fff
        font-size: 1em

    .photo
        border-radius: 100%
        height: 2.5em
        width: 2.5em
        background: #192734
        margin-left: 3.5em
        text-align: center

        img
            width: 100%
            height: 100%
            object-fit: cover
            border-radius: 100%

    .name
        font-weight: 700

    .username
        color: #8899a6
    #upload
        width: 100%
        height: 100%
        display: none